import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
// @mui
import { styled, useTheme } from '@mui/material/styles';
import { Box, Stack, Typography } from '@mui/material';
// utils
import { fDate } from 'utils/formatTime';//'../../../utils/formatTime';
import { fShortenNumber } from 'utils/formatNumber';
//

import AvatarWithUser from 'components/avatar/AvatarWithUser';
import { IconEye, IconMessageCircle, IconShare } from '@tabler/icons';

// ----------------------------------------------------------------------


// const StyledTitle = styled(Link)({
//   // height: 44,
//   overflow: 'hidden',
//   WebkitLineClamp: 2,
//   display: '-webkit-box',
//   WebkitBoxOrient: 'vertical',
// });



const StyledInfo = styled('div')(({ theme }) => ({
  // height: 60,
  display: 'flex',
  flexWrap: 'wrap',
  justifyContent: 'flex-end',
  overflow: 'hidden',
  marginTop: theme.spacing(1),
  color: theme.palette.text.disabled,
}));


// ----------------------------------------------------------------------

const BlogCard = ({ post, index }) => {
  const { id, title, cover, content, view, comment, share, author, createdAt } = post || {};
  const theme = useTheme();
  const navigate = useNavigate();
  const latestPostLarge = index === 0;
  const latestPost = index === 1 || index === 2;

  const POST_INFO = [
    // { number: comment, icon: 'eva:message-circle-fill' },
    { number: comment, icon: <IconMessageCircle /> },
    { number: view, icon: <IconEye /> },
    { number: share, icon: <IconShare /> },
  ];


  return (
    <Stack spacing={2} onClick={() => navigate(`/pages/blogs/${id}`)}>
      <Box
        component="img"
        height="260"
        src={cover}
        sx={{
          borderRadius: `${theme.shape.borderRadius}px`,
          my: 1
        }}
      />
      {/* <StyledCardMedia >
        <StyledCover alt={title} src={cover} />
        </StyledCardMedia> */}

      <Typography variant='h5' sx={{ color: theme.palette.text.default }}>
        {title?.substr(0, 60)}{title.length > 60 ? '...' : ''}
      </Typography>
      <Typography gutterBottom variant="body1" sx={{ height: 66, color: 'text.secondary', display: 'block', overflow: 'hidden' }}>
        {content?.substr(0, 120)}{content.length > 160 ? '...' : ''}
      </Typography>
      <AvatarWithUser name={author.name} avatar={author.avatarUrl} createdAt={fDate(createdAt)} />
      <StyledInfo>
        {POST_INFO.map((info, index) => (
          <Box
            key={index}
            sx={{
              display: 'flex',
              alignItems: 'center',
              ml: index === 0 ? 0 : 1.5,
              ...((latestPostLarge || latestPost) && {
                color: 'grey.500',
              }),
            }}
          >
            {info.icon}
            <Typography variant="caption">{fShortenNumber(info.number)}</Typography>
          </Box>
        ))}
      </StyledInfo>

    </Stack>
  );
}

BlogCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number,
};

export default BlogCard; 